<template>
  <div>
    <h2>Posts</h2>
    <form @submit.prevent="edit ? updatePost(post) : addPost()" class="mb-3">
      <div class="form-group">
        <input v-model="post.title" type="text" class="form-control" placeholder="Title">
      </div>
      <div class="form-group">
        <textarea v-model="post.body" type="text" class="form-control" placeholder="Body"></textarea>
      </div>

      <button
        type="submit"
        v-text="edit ? 'Update Post' : 'Add Post'"
        v-bind:class="edit ? 'btn-success' : 'btn-info'"
        class="btn btn-block"
      ></button>
    </form>
    <div v-show="posts.length > 0" class="card card-body mb-2" v-for="post in posts" v-bind:key="post._id">
      <h3 class="card-title">{{post.title}}</h3>
      <p class="lead">{{post.body}}</p>
      <hr>
      <button @click="editPost(post)" class="btn btn-success mb-1">Edit</button>
      <button @click="deletePost(post._id)" class="btn btn-danger">Delete</button>
    </div>
    <div v-show="posts.length == 0" class="card card-body bg-info text-light mb-5">
      <p class="lead text-center font-weight-bold">No posts yet ? Add your first post :D</p>
    </div>
  </div>
</template>

<script>
const axios = require("axios");

export default {
  name: "PostsContainer",
  data() {
    return {
      posts: [],
      post: {
        _id: "",
        title: "",
        body: ""
      },
      edit: false
    };
  },
  created() {
    this.fetchPosts();
  },
  methods: {
    fetchPosts(page_link) {
      this.setScroll();
      let page_url = "api/posts";
      axios
        .get(page_url)
        .then(res => {
          console.log(res.data);
          this.posts = res.data;
        })
        .catch(err => console.log(err));
    },
    addPost() {
      // Add post
      axios
        .post("api/posts", this.post)
        .then(res => {
          this.post.title = "";
          this.post.body = "";
          alert("Post added successfully!");
          this.fetchPosts();
        })
        .catch(err => console.log(err));
    },
    editPost(post) {
      this.edit = true;
      this.post._id = post._id;
      this.post.title = post.title;
      this.post.body = post.body;
      this.setScroll();
    },
    updatePost(post) {
      axios
        .put(`api/posts/${post._id}`, post)
        .then(res => {
          this.edit = false;
          this.setData();
          alert("Post updated successfully!");
          this.fetchPosts();
        })
        .catch(err => console.log(err));
    },
    deletePost(id) {
      if (confirm("Are you sure ?")) {
        axios
          .delete(`api/posts/${id}`)
          .then(res => {
            alert("Post deleted successfully!");
            this.fetchPosts();
          })
          .catch(err => console.log(err));
      }
    },
    setScroll() {
      window.scrollTo({
        top: 0,
        left: 0,
        behavior: "smooth"
      });
    },
    setData() {
      this.post._id = "";
      this.post.title = "";
      this.post.body = "";
    }
  }
};
</script>
